@charset "UTF-8";

.hidden {
  display: none;
}

body {
  font-family: "OpenSans";
}

.container {
  padding: 0 !important;
  margin: 0 auto;
  width: 100%;
  box-sizing: border-box;
}

ul,
ol {
  list-style: none;
}

header {
  display: flex;
  padding: 10px 30px 5px 30px;
}

header .logo {
  max-width: 40px;
  height: 40px;
  background: #8E8E8E;
  border-radius: 100%;
  float: left;
  margin-right: 50px;
  width: 10%;
  margin-top: 5px;
}

header .logo img {
  display: block;
  margin: 0 auto;
  padding-top: 11px;
}

header .menu {
  width: 40%;
  float: left;
}

header .menu h1 {
  margin-bottom: 10px;
}

header .menu ul li {
  float: left;
  margin-right: 50px;
}

header .menu ul li a {
  color: #000000;
  font-size: 16px;
  line-height: 22px;
  cursor: pointer;
  padding-bottom: 3px;
}

header .menu ul li a:hover {
  color: #3FB3F6;
  font-weight: 600;
  border-bottom: 2px solid #3FB3F6;
}

.carousel {
  width: 100%;
  position: relative;
}

.carousel .carousel-block {
  padding: 20px 0;
  background-size: cover;
}

.carousel .carousel-block .title-carousel {
  width: 350px;
  color: #FFFFFF;
  font-size: 28px;
  font-family: 'Open Sans Condensed';
  line-height: 34px;
  text-transform: uppercase;
}

.carousel .carousel-block .detail-txt {
  width: 350px;
  opacity: 0.7;
  color: #FFFFFF;
  font-size: 16px;
  font-style: italic;
  line-height: 22px;
  margin-top: 10px;
}

.carousel .content-carousel {
  width: 80%;
  margin: 0 auto;
  position: relative;
}

.carousel .content-carousel .img-content {
  object-fit: cover;
  width: 361px;
  height: 220px;
  position: absolute;
  top: 60%;
  transform: translateY(-50%);
  right: 40px;
}

.carousel .content-carousel .statistic li {
  float: left;
}

.carousel .content-carousel .statistic li p {
  color: #3FB3F6;
  font-size: 14px;
  font-weight: 600;
  line-height: 19px;
  margin: 10px;
  margin-top: 18px;
}

.carousel .content-carousel .statistic li p.date {
  margin-left: 0;
}

.carousel .content-carousel .statistic li p.spot {
  width: 117px;
  border-radius: 100px;
  background-color: rgba(255, 255, 255, 0.2);
  font-size: 14px;
  font-weight: 600;
  line-height: 19px;
  color: #fff;
  text-align: center;
  padding: 7px 0;
  margin-top: 12px;
}

.carousel button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 15px;
  height: 40px;
  border: none;
  box-shadow: none;
}

.carousel button.arrow-right {
  right: 40px;
  background: url(./images/arrow1.png) no-repeat;
  background-size: 100%;
}

.carousel button.arrow-left {
  left: 40px;
  background: url(./images/arrow.png) no-repeat;
  background-size: 100%;
}

.content-catalog {
  width: 80%;
  margin: 40px auto;
}

.content-catalog .side-bar-left {
  width: 22%;
  max-width: 242px;
  float: left;
}

.content-catalog .side-bar-left .item {
  padding: 20px;
  ackground-color: #FFFFFF;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.3);
  margin-bottom: 15px;
}

.content-catalog .side-bar-left .item.tag-block p {
  padding-left: 0;
}

.content-catalog .side-bar-left .title-sidebar {
  color: #B8B8B8;
  font-size: 10px;
  font-weight: bold;
  line-height: 14px;
  padding-left: 0;
}

.content-catalog .side-bar-left li {
  margin: 10px 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.content-catalog .side-bar-left ol,
.content-catalog .side-bar-left ul {
  margin-top: 15px;
}

.content-catalog .side-bar-left ul p {
  color: #3FB3F6;
  font-size: 14px;
  line-height: 19px;
}

.content-catalog .side-bar-left span {
  color: #B8B8B8;
  font-size: 14px;
  font-weight: 600;
  line-height: 19px;
}

.content-catalog .side-bar-left p {
  color: #494949;
  font-size: 14px;
  font-weight: 600;
  line-height: 19px;
  display: inline;
  padding-left: 5px;
  font-weight: normal;
}

.content-catalog .side-bar-left .redeem-block p {
  position: relative;
  padding-left: 25px;
}

.content-catalog .side-bar-left .redeem-block p:before {
  position: absolute;
  left: 0;
  content: '';
  background: url(./images/arrow3.png) no-repeat;
  height: 15px;
  width: 16px;
  top: 2px;
}

.content-catalog .content-right {
  width: 77%;
  float: right;
}

.content-catalog .course-card {
  margin-bottom: 30px;
}

.content-catalog .course-card .course-block {
  width: 31.33%;
  background-color: #FFFFFF;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.3);
  padding: 5px;
  margin-right: 2%;
  position: relative;
  float: left;
  margin-bottom: 2%;
}

.content-catalog .course-card .course-block figure {
  overflow: hidden;
}

.content-catalog .course-card .course-block img {
  width: 100%;
  cursor: pointer;
  -webkit-transform: scale(1);
  transform: scale(1);
  -webkit-transition: .3s ease-in-out;
  transition: .3s ease-in-out;
}

.content-catalog .course-card .course-block img:hover {
  -webkit-transform: scale(1.3);
  transform: scale(1.3);
}

.content-catalog .course-card .course-block .info-course {
  padding: 0 5px;
}

.content-catalog .course-card .course-block .info-course span {
  color: #B8B8B8;
  font-size: 10px;
  font-weight: bold;
  line-height: 14px;
  display: block;
  margin: 5px 0;
}

.content-catalog .course-card .course-block .info-course h3 {
  color: #494949;
  font-size: 16px;
  line-height: 18px;
  margin-bottom: 15px;
  font-family: "Open Sans Condensed";
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  text-transform: uppercase;
}

.content-catalog .course-card .course-block .info-course a {
  display: block;
  color: #3FB3F6;
  font-size: 10px;
  font-weight: bold;
  line-height: 14px;
}

.content-catalog .course-card .course-block .stamp {
  position: absolute;
  top: 15px;
  left: 10px;
}

.content-catalog .course-card .course-block .stamp a {
  color: #FFFFFF;
  font-size: 10px;
  font-weight: bold;
  line-height: 14px;
  padding: 4px 10px 4px 20px;
  border-radius: 2px;
  background-color: #3FB34F;
}

.content-catalog .course-card .course-block .stamp a.start {
  padding: 4px 10px;
  background-color: #3FB3F6;
}

.content-catalog .course-card .course-block .stamp a.enroll:before {
  position: absolute;
  content: '✔';
  color: #fff;
  width: 6px;
  height: 6px;
  top: 0;
  left: 9px;
}

.content-catalog .course-card .course-block:nth-child(3n+3) {
  margin-right: 0;
}




