.carousel-content {
  display: inline-block;
  overflow: hidden;
  outline: 0px;
}

.carousel-content--image {
  border: 1px solid #fff;
  position: relative;
}

.animation--next-enter {
  transform: translate(100%, 0);
  transition: transform 1.5s;
}

.animation--next-enter.animation--next-enter-active {
  transform: translate(0, 0);
}

.animation--next-leave {
  left: 0;
  position: absolute;
  top: 0;
  transform: translate(0, 0);
  transition: transform 1.5s;
}

.animation--next-leave.animation--next-leave-active {
  transform: translate(-100%, 0);
}

.animation--previous-enter {
  transform: translate(-100%, 0);
  transition: transform 1.5s;
}

.animation--previous-enter.animation--previous-enter-active {
  transform: translate(0, 0);
}

.animation--previous-leave {
  left: 0;
  position: absolute;
  top: 0;
  transform: translate(0, 0);
  transition: transform 1.5s;
}

.animation--previous-leave.animation--previous-leave-active {
  transform: translate(100%, 0);
}

.example-enter {
  opacity: 0.01;
}

.example-enter.example-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in;
}

.example-leave {
  opacity: 1;
}

.example-leave.example-leave-active {
  opacity: 0.01;
  transition: opacity 300ms ease-in;
}




